import { Link, useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { Users, UtensilsCrossed, Calendar, FileText, LayoutDashboard, Menu, Store, Flag, Ticket, ClipboardList, Search, Star, Images, Shield, ShieldAlert, BookOpen, Settings, PanelLeftClose, PanelLeft, BarChart3, ChevronDown, MessageSquarePlus, Phone, ShoppingBag, Bell, Mail, HelpCircle, Ban, VolumeX, Coins, Package, Bot, MessagesSquare, MailCheck, UserPlus } from "lucide-react";
import { Sidebar, SidebarContent, SidebarGroup, SidebarGroupContent, SidebarGroupLabel, SidebarMenu, SidebarMenuButton, SidebarMenuItem, useSidebar, SidebarFooter } from "@/components/ui/sidebar";
import { useUserRole } from "@/hooks/useUserRole";
import { Button } from "@/components/ui/button";
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible";
import { useState } from "react";

const AdminSidebar = () => {
  const { t } = useTranslation();
  const { state, toggleSidebar } = useSidebar();
  const { role } = useUserRole();
  const location = useLocation();
  const currentPath = location.pathname;

  const [openGroups, setOpenGroups] = useState<Record<string, boolean>>({
    statistics: true,
    security: true,
    daily: true,
    community: true,
    points: true,
    announcement: true,
    ai: true,
    appearance: true,
    development: true,
    help: true
  });

  const toggleGroup = (group: string) => {
    setOpenGroups(prev => ({
      ...prev,
      [group]: !prev[group]
    }));
  };

  const menuGroups = [{
    key: "statistics",
    label: t("admin.groups.statistics"),
    items: [{
      title: t("admin.dashboard"),
      url: "/admin",
      icon: LayoutDashboard,
      allowedRoles: ["admin", "supervisor"]
    }, {
      title: t("admin.analytics"),
      url: "/admin/analytics",
      icon: BarChart3,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.orderStats", "外卖订台统计"),
      url: "/admin/order-stats",
      icon: ShoppingBag,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.activityStatistics"),
      url: "/admin/activity-stats",
      icon: Calendar,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.restaurantStatistics"),
      url: "/admin/restaurant-stats",
      icon: Store,
      allowedRoles: ["admin"]
    }]
  }, {
    key: "security",
    label: t("admin.groups.security"),
    items: [{
      title: t("admin.activityRequests"),
      url: "/admin/activity-requests",
      icon: ClipboardList,
      allowedRoles: ["admin", "supervisor"]
    }, {
      title: t("admin.invitationCodes", "邀请码审核"),
      url: "/admin/invitation-codes",
      icon: UserPlus,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.userSecurity"),
      url: "/admin/user-security",
      icon: ShieldAlert,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.bannedManagement"),
      url: "/admin/banned-users",
      icon: Ban,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.mutedManagement"),
      url: "/admin/muted-users",
      icon: VolumeX,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.reports"),
      url: "/admin/reports",
      icon: Flag,
      allowedRoles: ["admin"]
    }, {
      title: t("joinUs.adminTitle"),
      url: "/admin/join-requests",
      icon: Store,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.sensitiveWords.title"),
      url: "/admin/sensitive-words",
      icon: Shield,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.comments.title", "评论管理"),
      url: "/admin/comments",
      icon: MessageSquarePlus,
      allowedRoles: ["admin"]
    }]
  }, {
    key: "daily",
    label: t("admin.groups.daily"),
    items: [{
      title: t("admin.users"),
      url: "/admin/users",
      icon: Users,
      allowedRoles: ["admin", "supervisor"]
    }, {
      title: t("admin.emailVerification", "邮箱验证"),
      url: "/admin/email-verification",
      icon: MailCheck,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.restaurants"),
      url: "/admin/restaurants",
      icon: UtensilsCrossed,
      allowedRoles: ["admin", "supervisor"]
    }, {
      title: t("admin.activities"),
      url: "/admin/activities",
      icon: Calendar,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.coupons"),
      url: "/admin/coupons",
      icon: Ticket,
      allowedRoles: ["admin"]
    }, {
      title: t("nav.couponQuery"),
      url: "/admin/coupon-query",
      icon: Search,
      allowedRoles: ["admin", "supervisor"]
    }]
  }, {
    key: "community",
    label: t("admin.groups.community", "社区管理"),
    items: [{
      title: t("admin.communityModeration.title", "社区审核"),
      url: "/admin/community-moderation",
      icon: ShieldAlert,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.communityPosts.title", "帖子管理"),
      url: "/admin/community-posts",
      icon: MessagesSquare,
      allowedRoles: ["admin"]
    }]
  }, {
    key: "points",
    label: t("admin.groups.points", "积分管理"),
    items: [{
      title: t("admin.points.title", "积分管理"),
      url: "/admin/points",
      icon: Coins,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.shop.title"),
      url: "/admin/shop-items",
      icon: Package,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.pointsSettings.title"),
      url: "/admin/points-settings",
      icon: Settings,
      allowedRoles: ["admin"]
    }]
  }, {
    key: "announcement",
    label: t("admin.groups.announcement", "公告管理"),
    items: [{
      title: t("admin.notifications", "发送通知"),
      url: "/admin/notifications",
      icon: Bell,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.emails", "发送邮件"),
      url: "/admin/emails",
      icon: Mail,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.feedback", "用户反馈"),
      url: "/admin/feedback",
      icon: MessageSquarePlus,
      allowedRoles: ["admin"]
    }]
  }, {
    key: "ai",
    label: t("admin.groups.ai", "AI聊天"),
    items: [{
      title: t("admin.aiSettings", "AI管理"),
      url: "/admin/ai-settings",
      icon: Bot,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.aiConfig", "AI配置管理"),
      url: "/admin/ai-config",
      icon: Settings,
      allowedRoles: ["admin"]
    }]
  }, {
    key: "appearance",
    label: t("admin.groups.appearance"),
    items: [{
      title: t("admin.homepageTemplates"),
      url: "/admin/homepage-templates",
      icon: Images,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.navigationSettings"),
      url: "/admin/navigation-settings",
      icon: Menu,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.siteSettings"),
      url: "/admin/site-settings",
      icon: Settings,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.joinUsSettings"),
      url: "/admin/site-settings#join-us",
      icon: MessageSquarePlus,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.contactSettings"),
      url: "/admin/site-settings#contact-info",
      icon: Phone,
      allowedRoles: ["admin"]
    }]
  }, {
    key: "development",
    label: t("admin.groups.development"),
    items: [{
      title: t("admin.logs"),
      url: "/admin/logs",
      icon: FileText,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.apiDocs"),
      url: "/admin/api-docs",
      icon: BookOpen,
      allowedRoles: ["admin"]
    }, {
      title: t("admin.batchRatings"),
      url: "/admin/ratings",
      icon: Star,
      allowedRoles: ["admin"]
    }]
  }, {
    key: "help",
    label: t("admin.groups.help", "帮助"),
    items: [{
      title: t("admin.userManual", "使用手册"),
      url: "/admin/user-manual",
      icon: HelpCircle,
      allowedRoles: ["admin", "supervisor", "merchant"]
    }]
  }];

  const isActive = (path: string) => {
    if (path === "/admin") {
      return currentPath === path;
    }
    // Exact match for specific paths to avoid partial matches
    if (path === "/admin/points" || path === "/admin/points-settings" || path === "/admin/shop-items") {
      return currentPath === path;
    }
    return currentPath.startsWith(path);
  };

  const filteredGroups = menuGroups.map(group => ({
    ...group,
    items: group.items.filter(item => item.allowedRoles.includes(role as string))
  })).filter(group => group.items.length > 0);

  return (
    <Sidebar collapsible="icon" className={state === "collapsed" ? "w-14" : "w-60"}>
      <SidebarContent className="pt-14">
        <SidebarGroup>
          <SidebarGroupLabel className={state === "collapsed" ? "text-center" : ""}>
            {state === "collapsed" ? <Menu className="h-4 w-4" /> : t("admin.management")}
          </SidebarGroupLabel>
          <SidebarGroupContent>
            <div className="mt-4 space-y-2 my-0">
              {filteredGroups.map(group => (
                <Collapsible 
                  key={group.key} 
                  open={state === "collapsed" ? true : openGroups[group.key]} 
                  onOpenChange={() => state !== "collapsed" && toggleGroup(group.key)}
                >
                  {state !== "collapsed" && (
                    <CollapsibleTrigger className="flex w-full items-center justify-between px-3 py-2 text-sm font-medium text-muted-foreground hover:text-foreground transition-colors">
                      <span>{group.label}</span>
                      <ChevronDown className={`h-4 w-4 transition-transform ${openGroups[group.key] ? "rotate-180" : ""}`} />
                    </CollapsibleTrigger>
                  )}
                  <CollapsibleContent>
                    <SidebarMenu>
                      {group.items.map(item => (
                        <SidebarMenuItem key={item.url}>
                          <SidebarMenuButton asChild isActive={isActive(item.url)}>
                            <Link to={item.url}>
                              <item.icon className="h-4 w-4" />
                              {state !== "collapsed" && <span>{item.title}</span>}
                            </Link>
                          </SidebarMenuButton>
                        </SidebarMenuItem>
                      ))}
                    </SidebarMenu>
                  </CollapsibleContent>
                </Collapsible>
              ))}
            </div>
          </SidebarGroupContent>
        </SidebarGroup>
      </SidebarContent>
      <SidebarFooter className="border-t">
        <Button variant="ghost" size="sm" onClick={toggleSidebar} className="w-full justify-center">
          {state === "collapsed" ? (
            <PanelLeft className="h-4 w-4" />
          ) : (
            <>
              <PanelLeftClose className="h-4 w-4 mr-2" />
              <span>{t("common.collapse", "收起")}</span>
            </>
          )}
        </Button>
      </SidebarFooter>
    </Sidebar>
  );
};

export default AdminSidebar;
