import { Outlet, Navigate } from "react-router-dom";
import { Suspense } from "react";
import { SidebarProvider, SidebarTrigger } from "@/components/ui/sidebar";
import { useAuth } from "@/contexts/AuthContext";
import { useUserRole } from "@/hooks/useUserRole";
import AdminSidebar from "@/components/admin/AdminSidebar";
import { useTranslation } from "react-i18next";
import { Loader2 } from "lucide-react";

// Lightweight loading spinner for admin page transitions
const AdminPageLoader = () => (
  <div className="flex items-center justify-center h-64">
    <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
  </div>
);

const AdminLayout = () => {
  const { t } = useTranslation();
  const { user } = useAuth();
  const { isAdmin, isSupervisor, isLoading } = useUserRole();

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">{t("common.loading")}</div>
      </div>
    );
  }

  if (!user || (!isAdmin && !isSupervisor)) {
    return <Navigate to="/" replace />;
  }

  return (
    <SidebarProvider>
      <div className="min-h-screen flex w-full overflow-hidden">
        <AdminSidebar />
        <main className="flex-1 flex flex-col bg-secondary/20 overflow-auto min-w-0">
          {/* Mobile header with menu trigger */}
          <header className="md:hidden sticky top-0 z-10 flex items-center h-14 px-4 border-b bg-background">
            <SidebarTrigger />
            <span className="ml-3 font-semibold">{t("admin.management")}</span>
          </header>
          <div className="flex-1 p-3 md:p-6 w-full max-w-full overflow-x-hidden">
            <Suspense fallback={<AdminPageLoader />}>
              <Outlet />
            </Suspense>
          </div>
        </main>
      </div>
    </SidebarProvider>
  );
};

export default AdminLayout;
